@import url('https://fonts.googleapis.com/css2?family=Sora:wght@100..800&display=swap');

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* Add the correct display in IE 9- */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

/* Remove the default focus outline */
a:focus,
button:focus {
  outline: none;
}

/* Set core elements to display: block by default */
summary {
  display: list-item;
}

:root {
  overflow-x: hidden;
}

html {
  overflow-x: hidden;
}

/* Set default line-height to 1 */
body {
  background-image: url(./assets/background/background-projects.png);
  background-repeat: no-repeat;
  background-size: cover;
  line-height: 1;
  font-size: 16px;
  padding: 0;
  font-family: "Sora", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  overflow-x: hidden;
}

/* Remove the list-style on list items */
ol,
ul {
  list-style: none;
}


/* Remove text-decoration on links */
a {
  color: white;
  text-decoration: none;
}

::-webkit-scrollbar {
  background: rgb(167, 23, 205);
  background: linear-gradient(176deg, rgba(167, 23, 205, 1) 0%, rgba(73, 39, 147, 1) 84%);
  width: 7px;
  /* Largeur de la barre de défilement */
}

::-webkit-scrollbar-thumb {
  height: 65px;
  background-color: white;
  cursor: pointer;
  /* Couleur du curseur de la barre de défilement */
  border-radius: 5px;
  /* Bord arrondi du curseur */
}

::-webkit-scrollbar-button {
  display: none;
  /* Masquer les boutons de la barre de défilement */
}